@import 'partials/mixins';
@import 'partials/variables';
@import 'partials/media-query';
.top-bar-right {
    text-align: left;
}
.topList {
    text-align: left;
}
.topList li {
    margin-right: 0;
    margin-left: 25px;
}
.topList li:last-child {
    margin: 0;
}
.topList li i {
    margin-right: 0;
    margin-left: 7px;
}
.navbar-default .navbar-nav{
  &.navbar-right{
    @include tablet{
      margin-right: 0;
      margin-left: 25px;
      float:left !important;
    }
  }
}
.navbar-brand {
  float: right;
}
.navbar-nav li.dropdown.singleDrop .dropdown-menu li.open .dropdown-menu {
  @include tablet {
    border-left: none;
    border-right: 10px solid transparent;
  }
}
.navbar .navbar-nav li.open .dropdown-menu,
.navbar .navbar-nav li.dropdown .dropdown-menu{
  @include tablet {
    min-width: 170px;
    padding: 15px 15px 20px 0;
  }
}
.navbar-nav li.open .dropdown-menu ul.dropdown-menu,
.navbar-nav li.dropdown .dropdown-menu ul.dropdown-menu {
  @include tablet {
    border: none;
    top: 0 !important;
    left: auto !important;
    right: 100%;
    margin-left: 0;
    margin-right: -5px;
    border-left: none;
    border-right: 10px solid transparent;
  }
}
.navbar-collapse ul.navbar-nav > li.dropdown > a:after{
  right: auto;
  left: 15px;
}
.navbar-collapse ul.navbar-nav li.dropdown li.dropdown > a:after{
  right: auto;
  left: 0;
  @include tablet {
    content: "\f104";
    right: auto;
    left: 15px;
  }
}
.navbar-collapse ul.navbar-nav li.dropdown.open li.dropdown > a:after{
  @include tablet {
    content: "\f104";
  }
}
.searchBox{
	display:inline-block;
	float:right;
	position:absolute;
	top: 21px;
    left: 70px;
    right: auto;
	@include tablet{
		top:69px;
    right: auto;
		left:15px;
	}
  .dropdown-menu-right {
    left: 0;
    right: auto;
  }
}
.rtl .main-slider{
  direction: ltr;
  text-align: right;
}
.rtl .owl-carousel.commentSlider,
.rtl .owl-carousel.partnersLogoSlider,
.rtl .owl-carousel.testimonialSlider,
.rtl .owl-carousel.postSlider{
  direction: ltr;
}
.rtl .owl-carousel.testimonialSlider,
.rtl .owl-carousel.postSlider{
  text-align: right;
}
.main-slider .common-inner a {
  display: inline-block;
}
.main-slider .slide-inner2{
	padding:10px 40px 10px 0;;
	@include mobile{
		padding:10px 60px 10px 7px;
	}
	@include tablet{
		padding:20px 70px 20px 7px;
	}
	@include desktops{
		padding:30px 70px 30px 15px;
	}
	@include large-desktops{
		padding:40px 85px 40px 10px;
	}
}
.banner-bottom-inner .media-left {
    margin-right: 0;
    margin-left: 20px;
    float: right;
    @include tablet {
      margin-right: 0;
      margin-left: 15px;
  }
  @include desktops {
    margin-right: 0;
    margin-left: 20px;
  }
}
.lawInfo ul li i {
    margin-right: 0;
    margin-left: 10px;
}
.progress-title {
    text-align: right;
}
.full-width-section .half-width-right{
  .free-request{
    padding:30px 30px 0 10px;
    @include desktops {
      padding:70px 50px 0 10px;
    }
  }
}
.countUpSection.paralax .icon {
    margin-right: 0;
    margin-left: 15px;
    float: right;
}
.media{
	.media-left{
		@include tablet {
			padding-right:0;
      padding-left:15px;
		}
		@include desktops {
			padding-right:0;
      padding-left:25px;
		}
  }
}
.media .list-inline li i,
.gridView > .caption > .list-unstyled li i,
.gridView > .caption > .list-inline li i {
    margin-right: 0;
    margin-left: 8px;
}
.footerBottom-inner .media-left {
    float: right;
}
.copyRight .list-inline{
  @include tablet{
    text-align: left;
  }
}
.sectionTitle h2:before {
    margin-right: 0;
    margin-left: 15px;
}
.sectionTitle h2:after {
  margin-left: 0;
  margin-right:15px;
}
.footerBottom-inner .footerTitle h4:after {
    margin-left: 0;
    margin-right:15px;
}
.footerInfo .input-group .input-group-addon{
  @include tablet {
    float: left;
  }
}
.input-group .form-control:first-child,
.input-group-addon:last-child{
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
}
.download-pdf i {
    margin-right: 0;
    margin-left: 10px;
}
.navbar-collapse.content-collapse ul.navbar-nav li a i {
    right: auto;
    left: 25px;;
}
.sectionTitle.text-left {
    text-align: right;
}
.pageTitleSection .pageTitleInfo.align-right .breadcrumb {
  @include tablet {
    float: left;
  }
}
.sticker {
  left: auto;
  right: 0;
}
.sidebar .input-group .btn{
  left: 0;
  right: auto;
}
.block .media .media-left {
    padding-right: 0;
    padding-left: 15px;
    float: right;
}
.block .media {
    direction: rtl;
}
.block .media .list-inline li i{
  margin-left: 5px;
  margin-right: 0;
}
.errorMsg {
  @include tablet {
    text-align: left;
    padding-right: 0;
    padding-left: 50px;
    margin-right: 0;
    margin-left: 20px;
    border-right: none;
    border-left: 1px solid #dedede;
  }
}
.errorInfo .input-group .input-group-addon i:before {
    content: "\f060";
}
.errorInfo .input-group .input-group-addon{
  right: auto;
  left: 10px;
}
.formBtnArea.pull-left{
  float: right !important;
}
.detail-content ul li i {
    margin-right: 0;
    margin-left: 10px;
}
.detail-content .detail-left {
  @include tablet {
    border-right: none;
    border-left: 1px solid #dedede;
    padding-right: 0;
    padding-left: 30px;
  }
}
.consultation .input-group .form-control{
  float: right;
  @include mobile {
    margin-right: 0;
    margin-left: 5px;
  }
}
.consultation .input-group {
  @include tablet {
    float: left;
  }
}
.profile-contact span i {
    margin-right: 0;
    margin-left: 10px;
}
.profile-progress .progress-wrapper{
  @include tablet {
    padding: 0 30px 0 0;
  	border-left:none;
    border-right:1px solid #dedede;
  }
  @include desktops {
    padding: 0 50px 0 0;
  	margin-left: 0;
    margin-right: 10px;
  }
  @include large-desktops {
    padding: 0 50px 0 0;
  	margin-left: 0;
    margin-right: 20px;
  }
}
